"use strict";

window.addEventListener("load", init);

var nextPage = 1;
var button_next = document.querySelector(".next_table");
var button_prev = document.querySelector(".prev_table");

function init() {
  request(render_table, request.url);
  next_paginator();
  back_paginator();
}

function next_paginator() {
  button_next.addEventListener("click", function () {
    if (nextPage >= 1 || nextPage < 8) {
      nextPage++;
      request.url = "https://swapi.co/api/people/?page=" + nextPage;
      request(Rerender_table, request.url);
      return nextPage;
    }
  });
}

function back_paginator() {
  button_prev.addEventListener("click", function () {
    if (nextPage >= 2 || nextPage < 8) {
      nextPage--;
      request.url = "https://swapi.co/api/people/?page=" + nextPage;
      request(Backrender_table, request.url);
      return nextPage;
    }
  });
}

function request(callback, url) {
  var xhr = new XMLHttpRequest();
  xhr.open("GET", url, true);
  xhr.send();
  xhr.onreadystatechange = function () {
    if (xhr.readyState != 4) return;

    if (xhr.status != 200) {
      var errStatus = xhr.status;
      var errText = xhr.statusText;
      console.log(errStatus + ": " + errText);
    } else {
      var data = JSON.parse(xhr.responseText);
      //console.log(data);
      callback(data);
    }
  };
}

request.url = "https://swapi.co/api/people/?page=1";

var render_table = function render_table(data) {
  var table = document.querySelector("table");
  for (var i = 0; i < 10; i++) {
    var tr = document.createElement("tr");
    var td = document.createElement("td");
    var td_2 = document.createElement("td");
    var td_3 = document.createElement("td");
    var td_4 = document.createElement("td");
    var td_5 = document.createElement("td");
    var td_6 = document.createElement("td");

    td.innerHTML = data.results[i].name;
    td_2.innerHTML = data.results[i].gender;
    td_3.innerHTML = data.results[i].height;
    td_4.innerHTML = data.results[i].mass;
    td_5.innerHTML = data.results[i].birth_year;
    td_6.innerHTML = data.results[i].skin_color;

    tr.appendChild(td);
    tr.appendChild(td_2);
    tr.appendChild(td_3);
    tr.appendChild(td_4);
    tr.appendChild(td_5);
    tr.appendChild(td_6);
    table.appendChild(tr);
  }
};

var Rerender_table = function Rerender_table(data) {
  var table = document.querySelector("table");
  var arrOftd = document.querySelectorAll("td");
  var cntr = 0;
  for (var i = 0; i < data.results.length; i++) {
    arrOftd[cntr++].innerHTML = data.results[i].name;
    arrOftd[cntr++].innerHTML = data.results[i].gender;
    arrOftd[cntr++].innerHTML = data.results[i].height;
    arrOftd[cntr++].innerHTML = data.results[i].mass;
    arrOftd[cntr++].innerHTML = data.results[i].birth_year;
    arrOftd[cntr++].innerHTML = data.results[i].skin_color;
  }
};

var Backrender_table = function Backrender_table(data) {
  var table = document.querySelector("table");
  var arrOftd = document.querySelectorAll("td");
  var cntr = 0;
  for (var i = 0; i < data.results.length; i++) {
    arrOftd[cntr++].innerHTML = data.results[i].name;
    arrOftd[cntr++].innerHTML = data.results[i].gender;
    arrOftd[cntr++].innerHTML = data.results[i].height;
    arrOftd[cntr++].innerHTML = data.results[i].mass;
    arrOftd[cntr++].innerHTML = data.results[i].birth_year;
    arrOftd[cntr++].innerHTML = data.results[i].skin_color;
  }
};

/* UI features in header */

/* choose side */
var button_shoose = document.querySelector(".button_shoose");
var currency_side = document.querySelector(".currency_side i");
var side_classes = ["fab fa-sith", "fas fa-jedi"];
var side_prev = document.querySelector(".prev");
var side_next = document.querySelector(".next");

currency_side.setAttribute("class", side_classes[0]);

var counter_choose = 0;

side_next.addEventListener("click", function () {
  counter_choose++;
  if (counter_choose > side_classes.length - 1) {
    counter_choose = 0;
  }
  currency_side.setAttribute("class", side_classes[counter_choose]);
});

side_prev.addEventListener("click", function () {
  counter_choose--;
  if (counter_choose < 0) {
    counter_choose = side_classes.length - 1;
  }
  currency_side.setAttribute("class", side_classes[counter_choose]);
});

button_shoose.addEventListener("click", function () {
  side_prev.setAttribute("class", "d-none");
  side_next.setAttribute("class", "d-none");
  button_shoose.setAttribute("class", "d-none");

  currency_side.className += " current_side";

  var needs_you = document.querySelector(".text_wrapper .contact");
  if (currency_side.getAttribute("class") === "fab fa-sith current_side") {
    needs_you.innerHTML = "Sith is need you !";
  } else {
    needs_you.innerHTML = "Jedi is need you !";
  }
});

/* img slider */

var slider_img = ["../img/img_slider_1.jpg", "../img/img_slider_2.jpg", "../img/img_slider_3.jpg"];
var current_slider = document.querySelector(".current_slider");

var prev_slider = document.querySelector(".prev_slider");
var next_slider = document.querySelector(".next_slider");

var slider_counter = 0;
current_slider.setAttribute("src", slider_img[slider_counter]);

next_slider.addEventListener("click", function () {
  slider_counter++;
  if (slider_counter > slider_img.length - 1) {
    slider_counter = 0;
  }
  current_slider.setAttribute("src", slider_img[slider_counter]);
});

prev_slider.addEventListener("click", function () {
  slider_counter--;
  if (slider_counter < 0) {
    slider_counter = slider_img.length - 1;
  }
  current_slider.setAttribute("src", slider_img[slider_counter]);
});

setInterval(function () {
  slider_counter++;
  if (slider_counter > slider_img.length - 1) {
    slider_counter = 0;
  }
  current_slider.setAttribute("src", slider_img[slider_counter]);
}, 8000);

/* VALIDATION */
var book = document.querySelector(".book");
var validation_block = document.querySelector(".validation");

var login_adm = "Admin";
var password_adm = "Admin123";

var btn_data = document.querySelector(".btn_get_data");
btn_data.addEventListener("click", get_data);

function get_data() {
  var login = document.querySelector(".login").value;
  var password = document.querySelector(".password").value;

  chek_valid_data(login, password);
}

function chek_valid_data(login, password) {
  if (login === login_adm && password === password_adm) {
    localStorage.setItem("Login", login);
    localStorage.setItem("Role", "Admin");
    if (localStorage.getItem("Role") === "Admin") {
      render_main();
    }
  } else {
    var error = document.querySelector(".error");
    error.style.display = "block";
  }
}

function render_main() {
  var validation_block = document.querySelector(".validation");
  validation_block.setAttribute("class", "validation");
  validation_block.setAttribute("class", "d-none");

  book.setAttribute("class", "book");
  book.setAttribute("class", "d-flex");
  book.setAttribute("class", "align-items-center");
}

var sign_out = document.querySelector(".sign_out");
sign_out.addEventListener("click", out);

function out() {
  localStorage.removeItem("Login");
  localStorage.removeItem("Role");
  if (localStorage.getItem("Role") != "Admin") {
    console.log("WFT");
    book.setAttribute("class", "d-none");

    validation_block.classList = "validation d-flex justify-content-center align-items-center";
  }
}